import React, {Component} from 'react';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import Add from './Add';

export default class List extends Component {
    state = { 
        todos: [
            {Id: '1', Title: 'Task_1', Status: 'Done'},
            {Id: '2', Title: 'Task_2', Status: 'Pending'}
        ]
    };


    deltodo = (todo) => {
        const filtereditems = this.state.todos.filter(x => x.Id !== todo.Id);
        this.setState({
            todos: filtereditems
        });
    };

    editTodo = (x) => {
        this.setState(state => ({
            todos: state.todos.map(todo => {
                if (todo.Id === x.Id) {
                    return{
                        ...todo,
                        Status: todo.Status === "Done" ? "Pending" :
                        "Done"
                    };
                } else {
                    return todo;
                }
            })
        }));
    };

    AddTodo = (todo) => {
        this.setState ({
            todos: [...this.state.todos, todo]
        });
    };

    render(){
        return(
            <div>
                <Add onAdd={this.AddTodo} />
                <h1>Todo List</h1>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Id</th>
                            <th>Title</th>
                            <th>Status</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.state.todos.map(x => {
                            return(
                                <tr key={x.Id}>
                                    <td >{x.Id}</td>
                                    <td>{x.Title}</td>
                                    <td style = {{color: x.Status === "Done" ? "green": "red"}}>{x.Status}</td>
                                    <td>
                                    <button className='btn btn-primary' onClick={() => this.deltodo(x)}>
                                        <span>
                                            <FontAwesomeIcon icon='trash' />
                                        </span>
                                        </button>
                                        <button className='btn btn-primary' onClick={()=> this.editTodo(x)}>
                                        <span>
                                            <FontAwesomeIcon icon='edit' />
                                        </span>
                                        </button>
                                        </td>
                                </tr>

                            );
                        })}
                    </tbody>

                </table>
                
            </div>
        );
    };
};