import React, {Component} from 'react';

class Add extends Component {
    state = {
        Id: "",
        Title: "",
        Status: "Pending", 
    };

    ChangeTodoId = (event) => {
        this.setState({
            Id: event.target.value
        });
    };

    ChangeTodoTitle = (event) => {
        this.setState({
            Title: event.target.value
        });
    };

    ChangeTodoStatus = (event) => {
        this.setState({
            Status: event.target.value
        });
    };

    ChangeTodoSubmit = (event) => {
        event.preventDefault();
        this.props.onAdd({
            Id: this.state.Id,
            Title: this.state.Title,
            Status: this.state.Status
        });

        this.setState({
            Id: "",
            Title: "",
            Status: "Pending"
        });
    };

    render(){
        return(
            <div>
                <h3>Add ToDo</h3>
                <form>
                    <div className="form-group" >
                        <input value={this.state.Id} className="form-control" onChange={this.ChangeTodoId} placeholder="Enter Id" />
                    </div>
                    <div className="form-group" >
                        <input value={this.state.Title} className="form-control"  onChange={this.ChangeTodoTitle} placeholder="Enter Title" />
                    </div>
                    <div className="form-group">
                        <select value={this.state.Status} className="form-control" onChange={this.ChangeTodoStatus} >
                            <option value="Done" >Done</option>
                            <option value="Pending" >Pending</option>
                        </select>
                    </div>
                    <button type="submit" className="form-control btn btn-primary" onClick={this.ChangeTodoSubmit} >Add Todo</button>
                </form>
            </div>
        )
    }

};

export default Add;